.App {
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.box1, .box2, .box3,.box4,.box5, .box6, .box7,.box8,.box9, .box10, .box11,.box12{
    height: 100px;
    width: 100px;
    padding: 10px;
    margin: 10px;
}
.box{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px;
}
.sqr, .rect,.tri,.cir{
    padding: 10px;
    margin: 10px;
}
.box1, .box2, .box3,.box4{
    background-color: orange; 
}
.box5, .box6, .box7,.box8{
    background-color: #f0f0f0; 
}
.box9, .box10, .box11,.box12{
    background-color: green; 
}